import React from "react";
import "./App.css";
import spin2 from "./assets/spin2.svg";
import spin3 from "./assets/spin3.svg";
import spin4 from "./assets/spin4.svg";
import spin5 from "./assets/spin5.svg";
import spin6 from "./assets/spin6.svg";
import spin7 from "./assets/spin7.svg";
import * as Data from "./assets/FIX.json";
import Lottie from "react-lottie";

let options = {
    loop: false,
    autoplay: true,
    animationData: Data.default,
    rendererSettings: {
        preserveAspectRatio: "xMidYMax none",
    },
};

const stop = {
    loop: false,
    autoplay: false,
    animationData: Data.default,
    rendererSettings: {
        preserveAspectRatio: "xMaxYMax none",
    },
};

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedSvg: 1,
            playLottie: false,
            isStopped: true,
            contentDecider: 0,
            contentToRender: [
                "Make a team of 2 to 6 people and come up with a team name!",
                "In the screens located in the main aisle, players can scan their wristband anytime to see their live-score.",
                "Each player gets an RFID wristband in which there are all his personal and his teamâ€™s information.",
                "Outside the rooms there is a display where players can choose the difficulty level of the game. Point - system is linked to the difficulty level chosen for each challenge!",
                "Outside each entrance there is a light.If the light is green the room is free,if the light is red another team is inside trying to beat the challenge and you have to wait! In order to have access in the room, all players should scan their wristbands.By the time the last player scans its wristband, the time starts counting down!",
                "Inside the room, if the red light is flashing you have failed to beat the challenge. However, you can try again as many times as you wish! If the light is green, congratulations! You have beaten the challenge!",
            ],
        };
    }

    iconClickHandler = (selectedSvg) => {
        if (this.state.isStopped) {
            this.setState(
                {
                    selectedSvg,
                    playLottie: true,
                    isStopped: false,
                },
                () => {
                    document.getElementById("content").classList.add("fade-in");
                    const animationTime = 1500;
                    setTimeout(() => {
                        this.setState({
                            contentDecider: this.state.selectedSvg - 1,
                        });
                    }, animationTime / 2);
                    setTimeout(() => {
                        document
                            .getElementById("content")
                            .classList.remove("fade-in");
                        this.setState({
                            playLottie: false,
                            isStopped: true,
                        });
                    }, animationTime);
                }
            );
        }
    };

    render() {
        return (
            <div className="App">
                <div>
                    {/* relative div to have absolutes inside */}
                    <div className="wrapper">
                        <p className="content" id="content">
                            {
                                this.state.contentToRender[
                                    this.state.contentDecider
                                ]
                            }
                        </p>
                        <Lottie
                            eventListeners={[
                                {
                                    eventName: "complete",
                                    callback: () => {
                                        console.log("hello");
                                    },
                                },
                            ]}
                            isStopped={this.state.isStopped}
                            isClickToPauseDisabled={!this.state.playLottie}
                            options={this.state.playLottie ? options : stop}
                            className="wrapped_lottie"
                        />
                        <img
                            src={spin6}
                            alt=""
                            onClick={() => this.iconClickHandler(1)}
                            style={
                                this.state.selectedSvg !== 1
                                    ? { opacity: "0.1" }
                                    : undefined
                            }
                            className="wrapped_svg_around1"
                        />
                        <img
                            src={spin7}
                            alt=""
                            onClick={() => this.iconClickHandler(2)}
                            style={
                                this.state.selectedSvg !== 2
                                    ? { opacity: "0.1" }
                                    : undefined
                            }
                            className="wrapped_svg_around2"
                        />
                        <img
                            src={spin5}
                            alt=""
                            className="wrapped_svg_around3"
                            onClick={() => this.iconClickHandler(3)}
                            style={
                                this.state.selectedSvg !== 3
                                    ? { opacity: "0.1" }
                                    : undefined
                            }
                        />
                        <img
                            src={spin4}
                            alt=""
                            className="wrapped_svg_around4"
                            onClick={() => this.iconClickHandler(4)}
                            style={
                                this.state.selectedSvg !== 4
                                    ? { opacity: "0.1" }
                                    : undefined
                            }
                        />
                        <img
                            src={spin3}
                            alt=""
                            className="wrapped_svg_around5"
                            onClick={() => this.iconClickHandler(5)}
                            style={
                                this.state.selectedSvg !== 5
                                    ? { opacity: "0.1" }
                                    : undefined
                            }
                        />
                        <img
                            src={spin2}
                            alt=""
                            className="wrapped_svg_around6"
                            onClick={() => this.iconClickHandler(6)}
                            style={
                                this.state.selectedSvg !== 6
                                    ? { opacity: "0.1" }
                                    : undefined
                            }
                        />
                    </div>
                </div>
            </div>
        );
    }
}

export default App;
